<template>
  <div>
    <a :href="ctaContent.link" :target="target">
      <button :class="this.ctaContent.style">{{ ctaContent.text }}</button>
    </a>
  </div>
</template>

<script>
  import Content from './cta-content'
  import { Props, Computed } from '../../assets/js/mixins';

  let ctaContent

  export default {
    mixins: [Props, Computed],
    data() {
      return {
        isLoading: false,
        ctaContent: {}
      }
    },
    computed: {
      target() {
        return this.ctaContent.newWindow ? "_blank" : "_self"
      }
    },
    created() {
      if(this.hasContentUrl) {
        ctaContent = new Content(this.content)
      }
    },
    beforeMount() {
      if (this.hasContentUrl) {
        let vm = this;
        ctaContent.formatCTAContent().then(() => {
          vm.ctaContent = ctaContent.content;
        }), (err) => {
          console.error("Failed to load AMP CA Content", err);
        }
      } else {
        this.ctaContent = this.content;
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../../assets/scss/mixins.scss";
  @import "../../assets/scss/variables.scss";

  button {
    border-width: 0 0 1px;
    cursor: pointer;
    display: inline-block;
    font-size: 1.3em;
    padding: 9px 11px 8px;
    text-align: center;
    text-decoration: none;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    -ms-border-radius: 4px;
    border-radius: 4px;
    -webkit-box-shadow: 0 2px 1px rgba(0,0,0,0.2);
    -moz-box-shadow: 0 2px 1px rgba(0,0,0,0.2);
    box-shadow: 0 2px 1px rgba(0,0,0,0.2);
    font-weight: normal;

    &.primary {
      background: $orange;
      border: 1px solid $orange;
      color: #fff;

      &:active {
        box-shadow: none;
      }
    }

    &.secondary {
      background: #EEEDEE;
      border: 1px solid #EEEDEE;
      color: #1A1A1A;

      &:active {
        box-shadow: none;
      }
    }
  }
</style>




