import ContentService from '../../assets/js/amplience-content-service';

export const makeCTA = ({ text, style, link, newWindow }) => ({
  text,
  style,
  link,
  newWindow
});

export default class CTAContentService extends ContentService {
  formatCTAContent() {
    return this.getContent().then((formatted) => {
      this.content = makeCTA(formatted);
    });
  }
}



